                </v-col>
                <v-col cols="12" sm="24" md="24">
                  <v-text-field value="Point round/math" disabled class="centered-input"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="3">
                  <v-text-field v-model="editedItem.value_round" label="value round" type="number"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="3">
                  <v-text-field v-model="editedItem.value_offset" label="value offset" type="number"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="3">
                  <v-text-field v-model="editedItem.value_operation" label="math operation"> </v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="3">
                  <v-text-field v-model="editedItem.value_raw" label="value raw"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="6">
                  <v-text-field v-model="editedItem.fallback_value" label="Fallback Value" type="number" hint="The empty value will return null"></v-text-field>
                </v-col>
                <v-col cols="12" sm="24" md="24">
                  <v-text-field value="Point value scale" disabled class="centered-input"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="3">
                  <v-text-field v-model="editedItem.input_min" label="input_min" type="number"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="3">
                  <v-text-field v-model="editedItem.input_max" label="input_max" type="number"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="3">
                  <v-text-field v-model="editedItem.scale_min" label="scale_min" type="number"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="3">
                  <v-text-field v-model="editedItem.scale_max" label="scale_max" type="number"></v-text-field>
                </v-col>
                <v-col cols="12" sm="24" md="24">
                  <v-text-field value="History" disabled class="centered-input"></v-text-field>
                </v-col>
                <!-- <v-col cols="12" sm="6"  md="3">
                  <v-checkbox v-model="editedItem.history_enable" label="Enable"></v-checkbox>
                </v-col> -->
                <v-col cols="12" sm="6" md="3">
                  <v-select v-model="editedItem.history_type" label="Type" :items="history_data_types"></v-select>
                </v-col>
                <v-col cols="12" sm="6" md="3">
                  <v-text-field v-model="editedItem.history_interval" label="Interval" type="number"></v-text-field>
                </v-col>
              </v-row>
            </v-form>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="close"> Cancel </v-btn>
          <v-btn color="blue darken-1" text @click="updatePoint(editedItem)" :disabled="!valid"> Save </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <!-- Delete dialog -->
    <confirm-dialog
      :is-open="dialogDelete"
      :on-close="closeDelete"
      :on-confirm="confirm"
      dialog-text="Do you want to delete ?"
    ></confirm-dialog>

    <!-- Mass Edit dialog-->
    <v-dialog v-model="dialogMassEdit" width="350px">
      <v-card>
        <v-card-title>Mass Edit</v-card-title>
        <v-card-text>
          <v-select
            v-if="editField === 'data_type'"
            v-model="editAll[editField]"
            :items="point_data_types"
            :label="toUpperTextKey(editField)"
          ></v-select>
          <v-select
            v-if="editField === 'data_endian'"
            v-model="editAll[editField]"
            :items="point_data_endians"
            :label="toUpperTextKey(editField)"
          ></v-select>
          <v-checkbox
            v-if="typeof editAll[editField] === 'boolean'"
            v-model="editAll[editField]"
            :label="toUpperTextKey(editField)"
          />
          <v-text-field
            v-if="typeof editAll[editField] === 'number'"
            v-model.number="editAll[editField]"
            min="0"
            :label="toUpperTextKey(editField)"
            type="number"
            @input="numberChange()"
          ></v-text-field>
          <v-text-field
            v-if="
              typeof editAll[editField] === 'string' &&
              editField !== 'function_code' &&
              editField !== 'data_type' &&
              editField !== 'data_endian'
            "
            v-model="editAll[editField]"
            :label="toUpperTextKey(editField)"
          ></v-text-field>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="dialogMassEdit = false">Close</v-btn>
          <v-btn color="primary darken-1" text @click="saveMassEdit(editAll[editField], editField)">Save</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-app>
</template>

<script>
import axios from '../../axios';
import _cloneDeep from 'lodash/cloneDeep';
import MqttConsole from '../Mqtt/MqttConsole';
import RightSidebar from '../Common/RightSidebar';
import ConfirmDialog from '../Common/ConfirmDialog';
import SystemStatusTable from '../Common/SystemStatusTable.vue';
import { DATA_TABLE_PROPS } from '../../constants/appConstant';
import * as modbusServerService from '../../services/modbusServerService';
import {
  RULES,
  TOPIC,
  POINT_HEADERS,
  POINT_ADVANCED_HEADERS,
  POINT_TYPES,
  POINT_DATA_TYPES,
  POINT_DATA_ENDIANS,
  HISTORY_DATA_TYPES,
  POINT_EDIT_ALL_DEFAULT,
} from './modbusConstant';
import { toUpperTextKey, conventionNaming } from '../../utils/utils';

export default {
  components: {
    MqttConsole,
    RightSidebar,
    ConfirmDialog,
    SystemStatusTable,
  },
  data: () => ({
    toUpperTextKey: toUpperTextKey,
    topic: TOPIC,
    pointHeaders: [...POINT_HEADERS],
    advancedHeaders: [...POINT_ADVANCED_HEADERS],
    point_types: [...POINT_TYPES],
    point_data_types: [...POINT_DATA_TYPES],
    point_data_endians: [...POINT_DATA_ENDIANS],
    history_data_types: [...HISTORY_DATA_TYPES],
    rules: { ...RULES },
    editAll: { ...POINT_EDIT_ALL_DEFAULT },
    dialog: false,
    dialogDelete: false,
    dialogMassEdit: false,
    tab: 0,
    action: '',
    network: {},
    valid: false,
    selected: [],
    editedItem: {},
    search: '',
    editField: '',
    intervalId: null,
    expanded: [],
    pointsOfDevice: [],
    dataTable: {},
    dirs: [
      { text: 'NETWORKS', link: '/modbus/networks' },
      { text: 'POINTS VIEW', disabled: true },
    ],
    advancedView: false,
    networks: [],
    panel: [0],
    tableProps: { ...DATA_TABLE_PROPS },
    loadingData: false,
  }),
  computed: {
    debugSubscriptions() {
      const { debugSubscriptions } = this.$store.state.mqttConfig;
      return debugSubscriptions.modbus;
    },
    setting() {
      return this.$store.state.setting;
    },
    networksData() {
      return this.$store.state.mbNetworks;
    },
  },
  watch: {
    'dialog'(val) {
      val || this.close();
    },
    'dialogDelete'(val) {
      val || this.closeDelete();
    },
    '$route.params.id': function (val, oldVal) {
      if (val != oldVal) {
        this.getAllPoints();
      }
    },
  },
  beforeRouteLeave(to, from, next) {
    clearInterval(this.intervalId);
    axios.post(`mqtt/unsubscribe`, { topic: this.topic }).finally(() => {
      next();
    });
  },
  mounted() {
    this.getAllPoints();

    //set auto refresh
    if (this.setting.refreshTime && this.setting.enableAutoRefresh) {
      this.intervalId = setInterval(() => {
        this.getAllPoints();
        if (this.setting.toastAutoRefresh) {
          this.$toasted.show(`Automatically refresh every ${this.setting.refreshTime}ms`, { type: 'info' });
        }
      }, this.setting.refreshTime);
    }
  },
  methods: {
    getAllPoints() {
      this.loadingData = true;
      modbusServerService.fetchNetworks({ with_children: true, points: true }).then(data => {
        this.networks = data;
        this.getDataTable(data);
        this.loadingData = false;
      });
    },
    getDataTable(networks) {
      this.dataTable = {};
      networks.forEach(network => {
        this.dataTable[network.name] = [];
        network.devices.forEach(device => {
          if (device.points.length > 0) {
            const pointsPerDevice = device.points.map(point => {
              const keys = Object.keys(point.point_store);
              if (keys && keys.length > 0) {
                keys.forEach(key => {
                  point = {
                    ...point,
                    [key]: point.point_store[key],
                    category: `DEVICE: ${device.name}`,
                    network_name: network.name,
                  };
                });
              }
              delete point.point_store;
              delete point.point_uuid;
              return point;
            });
            this.dataTable[network.name] = this.dataTable[network.name].concat(pointsPerDevice);
          }
        });
      });
    },
    editItem(item) {
      this.editedIndex = this.dataTable[item.network_name].findIndex(p => p.uuid == item.uuid);
      this.pointsOfDevice = this.dataTable[item.network_name].filter(point => point.device_uuid === item.device_uuid);
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },
    async clickDelete(item) {
      this.action = 'deleteSingle';
      this.dialogDelete = true;
      this.editedItem = item;
    },
    async clickDeleteMulti() {
      this.dialogDelete = true;
      this.action = 'deleteSelected';
    },
    async deleteSingle() {
      this.editedIndex = this.dataTable[this.editedItem.network_name].indexOf(this.editedItem);
      this.dataTable[this.editedItem.network_name].splice(this.editedIndex, 1);
      this.dataTable[this.editedItem.network_name] = [...this.dataTable[this.editedItem.network_name]];
      await modbusServerService.removePoint(this.editedItem.uuid);
      this.closeDelete();
    },
    async deleteSelected() {
      const promises = [];
      for (const item of this.selected) {
        this.editedItem = item;
        promises.push(this.deleteSingle());
      }
      await Promise.all(promises);
      this.selected = [];
    },
    async confirm() {
      await this[this.action]();
      this.dialog = false;
      this.action = '';
    },
    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = {};
        this.editedIndex = -1;
      });
    },
    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = {};
        this.editedIndex = -1;
      });
    },
    isExistingvalue(value, fieldName, item) {
      let points = _cloneDeep(this.pointsOfDevice);
      const index = points.findIndex(p => p.uuid == item.uuid);
      points.splice(index, 1);
      if (fieldName === 'register') {
        value = Number(value);
        if (item) {
          points = points.filter(p => p.function_code === item.function_code);
        }
      }
      return points.find(p => p[fieldName] == value) != null;
    },
    updatePoint(data) {
      data.fallback_value = !isNaN(parseFloat(data.fallback_value)) ? parseFloat(data.fallback_value) : null;
      modbusServerService.updatePoint(data.uuid, data).then(response => {
        const index = this.dataTable[data.network_name].findIndex(n => n.uuid == response.uuid);
        Object.assign(this.dataTable[data.network_name][index], response);
        this.$toasted.show('update success');
        this.close();
        this.expanded = [];
      });
    },
    backgroundColor(item) {
      let classColor = '';
      if (item.fault) {
        classColor = 'background-fault-true';
      } else {
        classColor = !item.enable ? 'background-enable-false' : '';
      }
      return classColor;
    },
    saveMassEdit(value, field) {
      for (const item of this.selected) {
        item[field] = value;
        this.updatePoint(item);
      }
      this.dialogMassEdit = false;
    },
    async openMassEditDialog(fieldName) {
      if (this.selected.length < 1) {
        return this.$toasted.show('Please select item first!', { type: 'error' });
      }
      this.dialogMassEdit = true;
      this.editField = fieldName;
      this.editAll = { ...POINT_EDIT_ALL_DEFAULT };
    },
    numberChange() {
      if(!this.editAll[this.editField] && this.editField !== 'fallback_value') return this.editAll[this.editField] = 0;
    },
    cancelEdit() {},
    openEdit(item) {
      this.editedIndex = this.dataTable[item.network_name].indexOf(item);
      this.editedItem = Object.assign({}, item);
    },
    closeEdit() {
      this.editedIndex = -1;
      this.editedItem = {};
    },
    expandItem({ item }) {
      this.editedIndex = this.dataTable[item.network_name].indexOf(item);
      this.editedItem = Object.assign({}, item);
    },
    onClickRefresh() {
      this.getAllPoints();
      this.$toasted.show(`successfully refresh`, { type: 'success' });
    },
    check(e, items) {
      if (!e) this.selected = [...this.selected].filter(point => point.device_uuid != items[0].device_uuid);
      else this.selected = [...this.selected].concat(items);
    },
    changeName(val) {
      this.editedItem.name = conventionNaming(val, this.setting);
    },
  },
};
</script>